.center{
    width: auto;
    background-color: $blanco;
    border-radius: 1rem;
    box-shadow: .5rem .5rem 1rem $gris;

    h1{
        text-align: center;
        padding: 2rem 0;
        border-bottom: 1px solid silver;
    }

    form{
        padding: 0 5rem 2rem 5rem ;
        box-sizing: border-box;

        .txt_field{
            position: relative;
            border-bottom: 2px solid $gris;
            margin: 3rem 0;
            input{
                width: 100%;
                padding: 0 .5rem;
                // height: 4rem;
                font-size: 1.6rem;
                border: none;
                background: none;
                outline: none;
            }
            label{
                position: absolute;
                top: 50%;
                left: .5rem;
                color: $gris;
                transform: translateY(-50%);
                font-size: 1.6rem;
                pointer-events: none;
                transition: .5s;
            }
            span::before{
                content: '';
                position: absolute;
                top: 4rem;
                left: 0;
                width: 0%;
                height: 2px;
                background-color: $verde;
                transition: .5s;
            }
            input:focus ~ label,
            input:valid ~ label{
                top: -5px;
                color: $azul;
                font-weight: $bold;
            }
            input:focus ~ span::before,
            input:valid ~ span::before{
                width: 100%;
            }
        }

        .pass{
            margin: -.5rem 0 2rem .5rem;
            color: $gris;
            cursor: pointer;

            &:hover{
                color: $rojo;
                font-weight: $bold;
            }
        }

        .sigup_link{
            margin: 3rem 0;
            text-align: center;
            font-size: 1.4rem;
            color: $gris;

            a{
                color: $azul;
                text-transform: uppercase;
                text-decoration: none;

                &:hover{
                    font-weight: $bold;
                    text-decoration: underline;
                }
            }
        }

    }
}