/**Modo oscuro**/
.derecha{
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet{
        align-items: end;
    }

    .dark-mode-btn{
        width: 3rem;
        margin-top: 2rem;
        margin-bottom: 2rem;
        //cambia al color opuesto sin necesidad de editores de imagen:
        filter: invert(100%);

        &:hover{
            cursor: pointer;
        }

        @include tablet{
            margin-top: 0;
        }
    }
}

/**Barra y Navegación**/
.barra{
    display: flex;
    flex-direction: column;
    padding-top: 3rem;
    align-items: center; //menu hamburguesa

    @include tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
}

.navegacion{
    // display: none;
    visibility: hidden;
    opacity: 0;
    transition: visibility .3s ease-in-out, opacity 0.5s linear;
    height: 0;

    @include tablet{
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }

    a{
        color: $blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        @include tablet{
            margin-right: 2rem;
            font-size: 1.8rem;

            &:last-of-type{
                margin-right: 0;
            }
        }
    }
}

/**Menú de hamburguesa**/
.mobile-menu{
    @include tablet{
        display: none;
    }

    img{
        width: 5rem;
    }
}

.mostrar{
    // display: flex;
    // flex-direction: column;

    // @include tablet{
    //     flex-direction: row;
    // }
    visibility: visible;
    opacity: 1;
    height: auto;
}