.testimonial{
    background-color: $rosa;
    font-size: 2rem;
    padding: 1rem;
    color: $blanco;
    border-radius: 2rem;

    blockquote{
        padding-left: 5rem;
        position: relative;

        &::before{
            content: '';
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            width: 4rem;
            height: 6rem;
            display: block;
            position: absolute;
            left: -1rem;
        }
    }

    p{
        color: $blanco;
        text-align: right;
    }
}