.dark-mode{
    background-color: darken($gris, 40);

    p{
        color: $gris;
    }
    a, h1, h2, h3, h4{
        color: $blanco;
    }

    .anuncio{
        background-color: darken($gris, 30);
        border: 1px solid darken($gris, 30);

        .precio{
            color: $rosa;
        }
    }

    .resumen-propiedad{
        .precio{
            color: $rosa;
        }
    }

    .texto-entrada{
        p{
            color: $blanco;
        }
    }

    .iconos-caracteristicas{
        img{
            filter: invert(100%);
        }
    }

    .txt-nosotros blockquote{
        color: $blanco;
    }

    .formulario{
        p{
            color: $beige;
        }
        legend, label{
            color: $blanco;
        }

        input:not([type="submit"]), textarea, select {
            background-color: darken($gris, 30);
            color: $beige;
            border: 1px solid darken($gris, 30);
        }
    }

/* - - - - - - - - TABLA DE PROPIEDADES - - - - - - */
    table.propiedades{    
        tbody{
            background-color: darken($gris, 30);
    
            td{
                color: $blanco;
            }
        }
    }

/* - - - - - - - - - - LOGIN - - - - - - - - - - - - */
    .center{
        background-color: darken($gris, 30);

        form{
            .txt_field{
                input{
                    color: $beige;
                }
                span::before{
                    background-color: $blanco;
                }
                input:focus ~ label,
                input:valid ~ label{
                    color: $naranja;
                }
            }

            .pass{
                &:hover{
                    color: $blanco;
                }
            }
            
            .sigup_link{
                a{
                    color: $naranja;
                }
            }
        }
    }

    .alerta{
        &.error{
            color: lighten($rojo, 10);
        }
    }
}