.btn-azul-block{
    @include boton($azul, block);
}

.btn-azul{
    @include boton($azul, inline-block);
}

.btn-rosa{
    @include boton($rosa, inline-block);
}

.btn-rojo-block{
    @include boton($rojo, block);
    width: 100%;
    padding: 1.5rem 1rem;
}

.btn-verde-block{
    @include boton($verde, block);
}

.btn-login{
    width: 100%;
    height: 5rem;
    border: .5px solid;
    background-color: $rosa;
    border-radius: 2.5rem;
    color: $blanco;
    font-weight: $bold;
    cursor: pointer;
    outline: none;

    &:hover{
        background-color: darken($rosa, 5);
        cursor: pointer;
        font-size: 1.6rem;
    }
}