  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.4rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 1.5rem;
}

.contenedor {
  width: 75%;
  max-width: 100rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.6rem;
}

h4 {
  font-size: 2.2rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.btn-azul-block {
  background-color: #006d77;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.btn-azul-block:hover {
  background-color: #00565e;
  cursor: pointer;
}

.btn-azul {
  background-color: #006d77;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.btn-azul:hover {
  background-color: #00565e;
  cursor: pointer;
}

.btn-rosa {
  background-color: #e07a5f;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.btn-rosa:hover {
  background-color: #dc684a;
  cursor: pointer;
}

.btn-rojo-block {
  background-color: #9b2226;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
  padding: 1.5rem 1rem;
}
.btn-rojo-block:hover {
  background-color: #861d21;
  cursor: pointer;
}

.btn-verde-block {
  background-color: #008000;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.btn-verde-block:hover {
  background-color: #006700;
  cursor: pointer;
}

.btn-login {
  width: 100%;
  height: 5rem;
  border: 0.5px solid;
  background-color: #e07a5f;
  border-radius: 2.5rem;
  color: #FFFFFF;
  font-weight: 700;
  cursor: pointer;
  outline: none;
}
.btn-login:hover {
  background-color: #dc684a;
  cursor: pointer;
  font-size: 1.6rem;
}

.dark-mode {
  background-color: #1a1a1a;
  /* - - - - - - - - TABLA DE PROPIEDADES - - - - - - */
  /* - - - - - - - - - - LOGIN - - - - - - - - - - - - */
}
.dark-mode p {
  color: grey;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #FFFFFF;
}
.dark-mode .anuncio {
  background-color: #343434;
  border: 1px solid #343434;
}
.dark-mode .anuncio .precio {
  color: #e07a5f;
}
.dark-mode .resumen-propiedad .precio {
  color: #e07a5f;
}
.dark-mode .texto-entrada p {
  color: #FFFFFF;
}
.dark-mode .iconos-caracteristicas img {
  filter: invert(100%);
}
.dark-mode .txt-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario p {
  color: #f4f1de;
}
.dark-mode .formulario legend, .dark-mode .formulario label {
  color: #FFFFFF;
}
.dark-mode .formulario input:not([type=submit]), .dark-mode .formulario textarea, .dark-mode .formulario select {
  background-color: #343434;
  color: #f4f1de;
  border: 1px solid #343434;
}
.dark-mode table.propiedades tbody {
  background-color: #343434;
}
.dark-mode table.propiedades tbody td {
  color: #FFFFFF;
}
.dark-mode .center {
  background-color: #343434;
}
.dark-mode .center form .txt_field input {
  color: #f4f1de;
}
.dark-mode .center form .txt_field span::before {
  background-color: #FFFFFF;
}
.dark-mode .center form .txt_field input:focus ~ label,
.dark-mode .center form .txt_field input:valid ~ label {
  color: #f77b28;
}
.dark-mode .center form .pass:hover {
  color: #FFFFFF;
}
.dark-mode .center form .sigup_link a {
  color: #f77b28;
}
.dark-mode .alerta.error {
  color: #c52b30;
}

/** UI **/
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #3d405b;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 40rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 45rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #FFFFFF;
  text-align: left;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}
.contenido-header a:hover {
  color: #e07a5f;
}

.footer {
  padding: 2.5rem 0;
  background-color: #3d405b;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding: 1rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .footer .copyright {
    text-align: end;
  }
}
.footer a:hover {
  color: #e07a5f;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}

/**Modo oscuro**/
.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: end;
  }
}
.derecha .dark-mode-btn {
  width: 3rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-btn:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-btn {
    margin-top: 0;
  }
}

/**Barra y Navegación**/
.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
  height: 0;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

/**Menú de hamburguesa**/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 8rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #f7f4e6;
  border: 1px solid #f0ecd3;
  margin-bottom: 1rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.precio {
  color: #e07a5f;
  font-weight: 700;
  font-size: 2rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-entrada a {
  color: #000000;
}
.texto-entrada h4 {
  margin: 0;
  margin-top: 2rem;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #e07a5f;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .texto-entrada h4 {
    margin-top: 0;
  }
}
.texto-entrada span {
  color: #006d77;
}

.testimonial {
  background-color: #e07a5f;
  font-size: 2rem;
  padding: 1rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 4rem;
  height: 6rem;
  display: block;
  position: absolute;
  left: -1rem;
}
.testimonial p {
  color: #FFFFFF;
  text-align: right;
}

.formulario p {
  font-size: 1.8rem;
  color: grey;
  margin: 0;
}
.formulario legend {
  font-size: 2rem;
  color: grey;
}
.formulario label {
  font-weight: bold;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]), .formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid grey;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

/**Páginas  Internas **/
@media (min-width: 768px) {
  .grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.txt-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}
@media (min-width: 768px) {
  .txt-nosotros blockquote {
    margin: 0;
  }
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
  border-radius: 1rem;
}
.alerta.error {
  color: #9b2226;
}
.alerta.exito {
  color: #008000;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  text-align: center;
}
table.propiedades thead {
  background-color: #006d77;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 1rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.img-propiedad {
  width: 20rem;
  margin: 1rem;
}

.center {
  width: auto;
  background-color: #FFFFFF;
  border-radius: 1rem;
  box-shadow: 0.5rem 0.5rem 1rem grey;
}
.center h1 {
  text-align: center;
  padding: 2rem 0;
  border-bottom: 1px solid silver;
}
.center form {
  padding: 0 5rem 2rem 5rem;
  box-sizing: border-box;
}
.center form .txt_field {
  position: relative;
  border-bottom: 2px solid grey;
  margin: 3rem 0;
}
.center form .txt_field input {
  width: 100%;
  padding: 0 0.5rem;
  font-size: 1.6rem;
  border: none;
  background: none;
  outline: none;
}
.center form .txt_field label {
  position: absolute;
  top: 50%;
  left: 0.5rem;
  color: grey;
  transform: translateY(-50%);
  font-size: 1.6rem;
  pointer-events: none;
  transition: 0.5s;
}
.center form .txt_field span::before {
  content: "";
  position: absolute;
  top: 4rem;
  left: 0;
  width: 0%;
  height: 2px;
  background-color: #008000;
  transition: 0.5s;
}
.center form .txt_field input:focus ~ label,
.center form .txt_field input:valid ~ label {
  top: -5px;
  color: #006d77;
  font-weight: 700;
}
.center form .txt_field input:focus ~ span::before,
.center form .txt_field input:valid ~ span::before {
  width: 100%;
}
.center form .pass {
  margin: -0.5rem 0 2rem 0.5rem;
  color: grey;
  cursor: pointer;
}
.center form .pass:hover {
  color: #9b2226;
  font-weight: 700;
}
.center form .sigup_link {
  margin: 3rem 0;
  text-align: center;
  font-size: 1.4rem;
  color: grey;
}
.center form .sigup_link a {
  color: #006d77;
  text-transform: uppercase;
  text-decoration: none;
}
.center form .sigup_link a:hover {
  font-weight: 700;
  text-decoration: underline;
}

.error404 {
  text-align: center;
  font-size: 6rem;
  font-weight: 700;
  color: #9b2226;
  line-height: 1.2;
}

.txt_error404 {
  text-align: center;
  color: #006d77;
  font-size: 2rem;
}

.centrarimg404 {
  display: flex;
  justify-content: center;
}

.img-error {
  width: 25rem;
}