.footer{
    padding: calc($separacion / 2) 0;
    background-color: $Morado;
    margin-bottom: 0;

    .contenedor-footer{
        padding: 1rem;
    }

    .copyright{
        color: $blanco;
        text-align: center;

        @include tablet{
            text-align: end;
        }
    }

    a:hover{
        color: $rosa;
    }

    .navegacion{
        visibility: visible;
        opacity: 1;
        height: auto;
    }
}