.alerta{
    padding: .5rem;
    text-align: center;
    font-weight: $bold;
    text-transform: uppercase;
    margin: 1rem 0;
    border-radius: 1rem;
    
    &.error{
        color: $rojo;
    }

    &.exito{
        color: $verde;
    }
}

table.propiedades{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    text-align: center;

    thead{
        background-color: $azul;

        th{
            color: $blanco;
            padding: 1rem;
        }
    }

    .imagen-tabla{
        width: 10rem;
    }
}

.img-propiedad{
    width: 20rem;
    margin: 1rem;
}